/**
 * Client Vault interface
 */
export interface Vault extends VaultData, ClientEntity {}

export function sVault(isPartial?: boolean) {
  return S.object()
    .id('#vault')
    .additionalProperties(false)
    .extend(sClientEntity(isPartial))
    .extend(sVaultData(isPartial))
}

export interface VaultData {
  // Ext data
  tokenId: string
  chainId: string
  typeId?: string
  categoryId?: string
  operatorIds?: string[]

  name: string
  address: string
  symbol: string
  protocol: Web3Protocol
  contractType: VaultContractType
  abi: AbiContract

  description?: Locales<string>
  shortDescription?: Locales<string>
  caption?: Locales<string>
  keyInfo?: VaultKeyInfo[]

  // Meta data
  metaContent?: MetaContent
  metaItems?: MetaItems

  visibility: VaultVisibility
  status: VaultStatus

  feePercentage: number
  harvestTokenIds?: string[]

  // Reward data
  rewardPrograms?: VaultRewardProgram[]
  rewardEmissions?: VaultRewardEmission[]

  // CDO
  cdo?: VaultCdo

  // CDO Epoch
  cdoEpoch?: VaultCdoEpoch

  // Pareto dollar
  paretoDollar?: VaultParetoDollar

  // Pareto token
  paretoToken?: VaultParetoToken

  // TODO: move this contract inside either CDO or CdoEpoch)
  // Strategy
  strategy?: VaultStrategy

  // POOL
  pools?: VaultPool[]

  // KYC
  kyc?: VaultKyc

  // Signatures
  signatures?: VaultSignature[]

  // External integrations
  integrations?: VaultIntegration[]

  // Maximum cap
  maxCap?: VaultMaxCap

  // Minimum deposit
  minDeposit?: VaultMinDeposit

  // Documents
  documents?: VaultDocument[]

  // Campaigns
  campaigns?: VaultCampaign[]

  // Siblings vaults
  siblings?: VaultSibling[]
}

export function sVaultData(isPartial?: boolean) {
  return S.object()
    .additionalProperties(false)
    .prop('tokenId', sStringId())
    .prop('chainId', sStringId())
    .prop('typeId', sStringId())
    .prop('categoryId', sStringId())
    .prop('operatorIds', S.array().items(S.string()))
    .prop('name', S.string())
    .prop('address', sBCAddress())
    .prop('symbol', S.string())
    .prop('protocol', sWeb3Protocol())
    .prop('contractType', sVaultContractType())
    .prop('abi', sAbiContract())
    .prop('description', sLocales())
    .prop('shortDescription', sLocales())
    .prop('caption', sLocales())
    .prop('keyInfo', S.array().items(sVaultKeyInfo()))
    .prop('metaContent', sMetaContent())
    .prop('metaItems', sMetaItems())
    .prop('visibility', sVaultVisibility())
    .prop('status', sVaultStatus())
    .prop('feePercentage', S.number())
    .prop('harvestTokenIds', S.array().items(S.string()))
    .prop('supportedTokenIds', S.array().items(S.string()))
    .prop('rewardPrograms', S.array().items(sVaultRewardProgram()))
    .prop('rewardEmissions', S.array().items(sVaultRewardEmission()))
    .prop('cdo', sVaultCdo())
    .prop('cdoEpoch', sVaultCdoEpoch())
    .prop('paretoDollar', sVaultParetoDollar())
    .prop('strategy', sVaultStrategy())
    .prop('pools', S.array().items(sVaultPool()))
    .prop('kyc', sVaultKyc())
    .prop('signatures', S.array().items(sVaultSignature()))
    .prop('integrations', S.array().items(sVaultIntegration()))
    .prop('maxCap', sVaultMaxCap())
    .prop('minDeposit', sVaultMinDeposit())
    .prop('documents', S.array().items(sVaultDocument()))
    .prop('campaigns', S.array().items(sVaultCampaign()))
    .prop('siblings', S.array().items(sVaultSibling()))
    .required(
      isPartial
        ? []
        : [
            'tokenId',
            'chainId',
            'name',
            'address',
            'symbol',
            'protocol',
            'contractType',
            'abi',
            'visibility',
            'status',
            'feePercentage',
          ]
    )
}

export interface VaultIntegrationParams {
  chainId?: number
}

export interface VaultIntegration {
  provider: VaultIntegrationProvider
  type: VaultIntegrationType
  params?: VaultIntegrationParams
}

export interface VaultIntegrationsData {
  APR?: number
}

export type VaultIntegrationProvider =
  | 'Usual'
  | 'Lido'
  | 'Instadapp'
  | 'Ethena'
  | 'Euler'
export type VaultIntegrationType = 'APR' | 'strategyAPR' | 'accountVaults'

export function sVaultIntegrationProvider() {
  return S.string().enum(['Usual', 'Lido', 'Instadapp', 'Ethena', 'Euler'])
}

export function sVaultIntegrationType() {
  return S.string().enum(['APR', 'strategyAPR'])
}

export function sVaultIntegration() {
  return S.object()
    .additionalProperties(false)
    .prop('provider', sVaultIntegrationProvider())
    .required()
    .prop('type', sVaultIntegrationType())
    .required()
    .prop('params', sVaultIntegrationParams())
}

export function sVaultIntegrationParams() {
  return S.object().additionalProperties(false).prop('chainId', S.number())
}

export function sVaultIntegrationsData() {
  return S.object().additionalProperties(false).prop('APR', S.number())
}

